(function() {
    'use strict';

    var NINumericFormatters = window.NINumericFormatters;

    // 'LVRelativeSeconds:Gx' or LVRelativeSeconds:0.##...#
    // Ex: 'LVRelativeSeconds:N4'
    // where N represents the Fixed Notation,
    // G represents the Significant Digits Precision and
    // x is a number representing the number of decimals/digits
    var parseTickFormatterType = function(format) {
        var colonMarkIndex, decimals, g, n,
            unknownFormat = { type: 'default' },
            parsedFormat = unknownFormat;

        if (format.indexOf('LVSI') === 0) {
            colonMarkIndex = 5;
            var formatSubstring = format.slice(colonMarkIndex);
            if (formatSubstring.indexOf('G') === -1 && formatSubstring.indexOf('N') === -1) {
                if (formatSubstring.indexOf('#') !== -1 && formatSubstring.lastIndexOf('#') === formatSubstring.length - 1) {
                    decimals = formatSubstring.lastIndexOf('#') - formatSubstring.indexOf('#') + 1;
                    parsedFormat = { type: 'si', N: false, G: false, decimals: decimals };
                } else if (formatSubstring === '0') {
                    parsedFormat = { type: 'si', N: false, G: false, decimals: 0 };
                } else {
                    parsedFormat = { type: 'si', N: false, G: false, decimals: null };
                }
            } else {
                decimals = parseInt(formatSubstring.slice(1));
                n = formatSubstring.indexOf('N') === 0;
                g = formatSubstring.indexOf('G') === 0;
                parsedFormat = { type: 'si', N: n, G: g, decimals: decimals };
            }
        }

        if (format.indexOf('LVRelativeSeconds') === 0) {
            colonMarkIndex = 18;
            var decFormatTypeSubstring = format.slice(colonMarkIndex);

            if (decFormatTypeSubstring.indexOf('G') === -1 && decFormatTypeSubstring.indexOf('N') === -1) {
                if (decFormatTypeSubstring.lastIndexOf('#') !== -1 && decFormatTypeSubstring.lastIndexOf('#') === decFormatTypeSubstring.length - 1) {
                    decimals = decFormatTypeSubstring.lastIndexOf('#') - decFormatTypeSubstring.indexOf('#') + 1;
                    parsedFormat = { type: 'dec', N: false, G: false, decimals: decimals };
                } else if (decFormatTypeSubstring === '0') {
                    parsedFormat = { type: 'dec', N: false, G: false, decimals: 0 };
                } else {
                    parsedFormat = unknownFormat;
                }
            } else {
                decimals = parseInt(decFormatTypeSubstring.slice(1));
                n = decFormatTypeSubstring.indexOf('N') === 0;
                g = decFormatTypeSubstring.indexOf('G') === 0;
                parsedFormat = { type: 'dec', N: n, G: g, decimals: g ? Math.min(decimals, 21) : decimals };
            }
        }

        return parsedFormat;
    };

    var getTickFormatter = function(format) {
        if (format.type === 'si' && format.G) {
            return getSiSignificatDigitsFormatter(format);
        } else if (format.type === 'si' && format.N) {
            return getSiDecimalFormatter(format);
        } else if (format.type === 'si' && typeof format.decimals === 'number') {
            return getSiDecimalFormatter(format);
        } else if (format.type === 'si' && format.decimals === null) {
            return siTickFormatter;
        } else if (format.type === 'dec' && format.G) {
            return getDecPrecisionNotationFormatter(format.decimals);
        } else if (format.type === 'dec' && format.N) {
            return getDecFixedNotationFormatter(format.decimals);
        } else if (format.type === 'dec' && format.decimals === 0) {
            return getDecFixedNotationFormatter(0, true);
        } else if (format.type === 'dec' && format.decimals >= 1) {
            return getDecFixedNotationFormatter(format.decimals);
        }

        return null;
    };

    var getSiSignificatDigitsFormatter = function(format) {
        return function siSignificantDigitsFormatter(num) {
            var siValue = NINumericFormatters.prototype.toSiNotation(num, format.decimals),
                value, extension;
            if (isNaN(Number(siValue))) {
                value = Number(siValue.slice(0, siValue.length - 1));
                extension = siValue.slice(siValue.length - 1);
            } else {
                value = Number(siValue);
                extension = '';
            }
            return NINumericFormatters.prototype.toDecPrecisionNotation(value, Math.min(format.decimals, 21)) + extension;
        };
    };

    var getSiDecimalFormatter = function(format) {
        return function siDecimalFormatter(num) {
            return NINumericFormatters.prototype.toSiNotation(num, format.decimals);
        };
    };

    var siTickFormatter = function (num, axis, precision, plot) {
        var decimals = axis.tickDecimals,
            log10AxisMin, croppedMin, croppedMax;

        if (axis.tickDecimals !== axis.options.tickDecimals) {
            // we recompute the precision given by flot by considering another axis range.
            // For example, if axis.min=1000 and axis.max=1200 we recompute the numbers of ticks for min=1 and max=1.2 which gives us 2 decimals needed instead of 0
            croppedMin = computeCroppedValue(axis.min);
            log10AxisMin = axis.min !== 0 ? Math.floor(Math.log(Math.abs(axis.min)) * Math.LOG10E) : 0;
            croppedMax = computeCroppedValue(axis.max, log10AxisMin);

            decimals = num !== 0 ? Math.abs(plot.computeValuePrecision(croppedMin, croppedMax, axis.direction, axis.options.ticks)) : 0;
        }

        if (precision) {
            var precisionDecimals = Math.abs(precision) <= 3 ? Math.abs(precision) + 1 : reduceNumberOfDecimals(num, precision, axis.max, axis.min);
            if (precisionDecimals > decimals) {
                //the precision of endpoints should not be smaller then the one used for major ticks
                return NINumericFormatters.prototype.toSiNotation(num, fixDecimalCountForSmallNumbers(num, precisionDecimals));
            }
        }

        return NINumericFormatters.prototype.toSiNotation(num, fixDecimalCountForSmallNumbers(num, decimals));
    };

    // Makes sure the number smaller than 1e-24 are not converted to zero
    var fixDecimalCountForSmallNumbers = function(num, decimals) {
        var exponent = Math.floor(Math.log(Math.abs(num)) * Math.LOG10E),
            minSiExtensionExponent = -24;
        if (exponent < minSiExtensionExponent - 1) {
            decimals += Math.abs(minSiExtensionExponent - 1 - exponent);
        }
        return decimals;
    }

    var getDecPrecisionNotationFormatter = function(decimals) {
        return function(num) {
            return NINumericFormatters.prototype.toDecPrecisionNotation(num, Math.min(decimals, 21));
        };
    };

    var getDecFixedNotationFormatter = function(decimals, dontRound) {
        return function decFixedNotationFormatter(num) {
            var value = dontRound ? num : roundUsing(Math.floor, num, decimals);
            return NINumericFormatters.prototype.toDecFixedNotation(value, decimals);
        };
    };

    //heuristic for recomputing the number of decimals to [|log10(|num|) + tickDecimals|]
    //where tickDecimals is compunted based on axis.max-axis.min
    var reduceNumberOfDecimals = function(num, tickDecimals, max, min) {
        var log10Max = Math.log(Math.abs(max)) * Math.LOG10E,
            log10Min = min !== 0 ? Math.log(Math.abs(min)) * Math.LOG10E : 0,
            orderSizeDif = Math.floor(Math.abs(log10Max - log10Min)),
            log10Value = num !== 0 ? Math.log(Math.abs(num)) * Math.LOG10E : 0,
            //the precision computed in flot will be positive for small interval and negative otherwise
            decimals = Math.abs(log10Value + tickDecimals);

        //the range of axis is big so we need to reduce the no of decimals
        if (orderSizeDif > 3) {
            return Math.ceil(decimals * 3 / orderSizeDif);
        }

        return Math.ceil(decimals);
    };

    var computeCroppedValue = function(val, roundingPower) {
        var log10Value = val !== 0 ? Math.floor(Math.log(Math.abs(val)) * Math.LOG10E) : 0,
            exp = Math.pow(10, log10Value),
            croppedVal = val;

        if (roundingPower > 0) {
            croppedVal /= Math.pow(10, Math.floor(roundingPower / 3) * 3);
        } else croppedVal *= Math.pow(10, log10Value % 3) / exp;

        return croppedVal;
    }

    var roundUsing = function(roundFunction, number, precisionDigits) {
        var precision = Math.pow(10, precisionDigits);
        var tempNumber = number * precision;
        tempNumber = roundFunction(tempNumber);

        return tempNumber / precision;
    };

    NINumericFormatters.parseTickFormatterType = parseTickFormatterType;
    NINumericFormatters.getTickFormatter = getTickFormatter;
})();
